import React from 'react'
import './main.less'
import { List, Picker, Button, WingBlank, WhiteSpace, Toast, ActivityIndicator } from 'antd-mobile';
import { createForm } from 'rc-form';
import { httpPost, httpGet } from '../../util/http';
import { isNumber } from '../../util/util';
import History from '../../util/history'

class PartsMain extends React.Component {
    state = {
        loading: false,
        citys: [],
        devices: [],
        parts: [],
        standards: []
    }
    componentDidMount() {
        this.fetchCityData()
    }
    fetchCityData = () => {
        httpGet('/parts/query/cities.json').then(res => {
            this.setState({
                citys: this.array2pickerData(res)
            })
        })
    }
    fetchDevicesData = (city) => {
        httpGet('/parts/query/devices.json', { city: city }).then(res => {
            this.setState({
                devices: this.array2pickerData(res)
            })
        })
    }
    fetchPartsData = (device) => {
        httpGet('/parts/query/parts.json', { city: this.props.form.getFieldValue('city'), device: device }).then(res => {
            this.setState({
                parts: this.array2pickerData(res)
            })
        })
    }
    fetchStandardsData = (part) => {
        httpGet('/parts/query/standards.json', { city: this.props.form.getFieldValue('city'), device: this.props.form.getFieldValue('device'), part: part }).then(res => {
            this.setState({
                standards: this.array2pickerData(res)
            })
        })
    }
    array2pickerData(ary) {
        return ary.map(each => {
            return {
                value: each,
                label: each
            }
        })
    }
    handleCityChange = data => {
        this.fetchDevicesData(data)
    }
    handledeviceChange = data => {
        this.fetchPartsData(data)
    }
    handlePartsChange = data => {
        this.fetchStandardsData(data)
    }
    submit() {
        this.props.form.validateFields().then(values => {
            console.log('form valid success', JSON.stringify(values))
            // let formData = new FormData()
            // Object.keys(values).map(key => {
            //     formData.append(key, values[key])
            // })
            // this.setState({ loading: true })
            // httpPost('/parts/query.json', formData).then(() => {
                // this.setState({ loading: false })
                History.push({
                    pathname: '/parts/result',
                    state: values
                })
            // }, err => {
            //     this.setState({ loading: false })
            //     console.log(err)
            //     Toast.fail(err)
            // })
        }, ({ errors }) => {
            for (let field in errors) {
                Toast.fail(this.props.form.getFieldError(field)[0], 1, null, false)
                break
            }
        })
    }
    // 字符串转合法数字
    numberNormalize(val) {
        const num = Number(val)
        if (val === '' || !isNumber(num)) {
            return val
        }
        return num
    }
    render() {
        const { getFieldProps } = this.props.form;
        const { loading, citys, devices, parts, standards } = this.state
        return (
            <form className='mainPage'>
                <List renderHeader={() => '配件查询'}>
                    <Picker
                        {...getFieldProps('city', {
                            onChange: this.handleCityChange,
                            rules: [{
                                required: true,
                                message: '请选择城市'
                            }],
                        })}
                        data={citys}
                        cols={1}
                        title="选择城市"
                        extra="请选择"
                    >
                        <List.Item arrow="horizontal">城市</List.Item>
                    </Picker>
                    <Picker
                        {...getFieldProps('device', {
                            onChange: this.handledeviceChange,
                            rules: [{
                                required: true,
                                message: '请选择设备'
                            }],
                        })}
                        data={devices}
                        cols={1}
                        title="选择设备"
                        extra="请选择"
                    >
                        <List.Item arrow="horizontal">设备名称</List.Item>
                    </Picker>
                    <Picker
                        {...getFieldProps('part', {
                            onChange: this.handlePartsChange,
                            rules: [{
                                required: true,
                                message: '请选择配件'
                            }],
                        })}
                        data={parts}
                        cols={1}
                        title="选择配件"
                        extra="请选择"
                    >
                        <List.Item arrow="horizontal">配件名称</List.Item>
                    </Picker>
                    <Picker
                        {...getFieldProps('standard', {
                            initialValue: ''
                        })}
                        data={standards}
                        cols={1}
                        title="选择规格"
                        extra="请选择"
                    >
                        <List.Item arrow="horizontal">规格</List.Item>
                    </Picker>
                </List>
                <WhiteSpace size='xl'></WhiteSpace>
                <WingBlank>
                    <Button type='primary' onClick={this.submit.bind(this)}>立即查询</Button>
                </WingBlank>
                <ActivityIndicator
                    toast
                    text="正在加载..."
                    animating={loading}
                />
            </form>
        )
    }
}
export default createForm()(PartsMain)