import React from 'react'
import './result.less'
import { Button, Modal, Toast, List, Flex, InputItem, TextareaItem } from 'antd-mobile'
import History from '../../util/history'
import { httpPost } from '../../util/http'
import { Brief } from 'antd-mobile/lib/list/ListItem'
import { createForm } from 'rc-form';

export default createForm()(class PartsResult extends React.Component {
  state = {
    result: History.location.state,
    list: [],
    modalVisiable: false,
    id: null,
    activeItemKey: null
  }
  componentDidMount() {
    const result = this.state.result
    if (!result) {
      this.requery()
      return
    }

    let formData = new FormData()
    Object.keys(result).map(key => {
      formData.append(key, result[key])
    })
    this.setState({ loading: true })
    httpPost('/parts/query.json', formData).then(res => {
      this.setState({ loading: false, list: res })
    }, err => {
      this.setState({ loading: false })
      console.log(err)
      Toast.fail(err)
    })
  }
  requery() {
    History.push('/parts')
  }
  yiyi = id => {
    if (id === this.state.activeItemKey) {
      this.setState({
        activeItemKey: null
      })
    } else {
      this.setState({
        activeItemKey: id
      })
    }
  }
  yiyiModal = (data) => {
    this.setState({
      modalVisiable: true,
      id: data.id,
      activeItemKey: null
    })
  }
  submit = () => {
    this.props.form.validateFields().then(values => {
      let formData = new FormData()
      formData.append('partId', this.state.id)
      formData.append('comment', values.comment)
      formData.append('contact', values.contact)
      httpPost('/parts/objection/add.json', formData).then(() => {
        this.closeModal()
        Toast.success('提交成功')
      }, err => {
        console.log(err)
        Toast.fail(err)
      })
    }, ({ errors }) => {
      for (let field in errors) {
        Toast.fail(this.props.form.getFieldError(field)[0], 1, null, false)
        break
      }
    })
  }
  closeModal = () => {
    this.setState({
      modalVisiable: false
    }, () => {
      this.props.form.resetFields()
    })
  }
  render() {
    const { result, list, modalVisiable, activeItemKey } = this.state
    const { form: { getFieldProps } } = this.props
    if (!result) return null
    return (
      <div className='parts-result'>
        <List>
          <List.Item>
            <span>查询结果</span>
            <Brief>
              <Flex>
                <Flex.Item className='result-title'>
                  <div>{result.city} &gt; {result.device} &gt; {result.part}{result.standard && <span> &gt; {result.standard}</span>}</div>
                </Flex.Item>
                <Button type='primary' size='small' onClick={this.requery}>重新询价</Button>
              </Flex>
            </Brief>
          </List.Item>
        </List>
        <List renderHeader={<ResultListHeader />}>
          {list.map(each => {
            return <div className={`swipe-wrap ${activeItemKey === each.id ? 'active' : ''}`}>
              <List.Item className='swipe-cover' key={each.id} onClick={() => this.yiyi(each.id)} extra={<ResultListRight data={each} />}>
                {each.device_name}-{each.part_name}{each.standard && <span>-{each.standard}</span>}
              </List.Item>
              <div className='swipe-content' onClick={() => this.yiyiModal(each)}><div>异议</div></div>
            </div>
          })}
        </List>
        <Modal
          transparent
          title='提出异议'
          visible={modalVisiable}
          footer={[{ text: '取消', onPress: this.closeModal },
          { text: '确认', onPress: this.submit }]}
        >
          <List className='modal-list'>
            <TextareaItem
              autoHeight
              placeholder='您对价格的意见'
              {...getFieldProps('comment', {
                rules: [{
                  required: true,
                  message: '请填写意见'
                }],
              })}></TextareaItem>
            <TextareaItem
              autoHeight
              placeholder='留下您的联系方式，电话+姓名'
              {...getFieldProps('contact', {
                rules: [{
                  required: true,
                  message: '请填写联系方式'
                }],
              })}></TextareaItem>
          </List>

        </Modal>
      </div>
    )
  }
})

function ResultListHeader() {
  return <Flex>
    <Flex.Item style={{ flex: 1 }}>名称</Flex.Item>
    <Flex.Item style={{ textAlign: 'right' }}>价格区间 <div style={{ display: 'inline-block', width: '50px' }}>单位</div></Flex.Item>
  </Flex>
}

function ResultListRight(props) {
  return <div style={{ textAlign: 'right' }}>{props.data.price}<div style={{ display: 'inline-block', width: '35px' }}>{props.data.unit}</div></div>
}