import React from 'react'
import { List, Modal, Radio } from "antd-mobile";

export default function ModalPicker(props) {
    const { title, visible, onClose, dataSource, value, onOk } = props
    const onItemClick = data => {
        onOk(data)
        onClose()
    }
    return <Modal
        visible={visible}
        title={title}
        transparent
        onClose={onClose}>
        <List style={{ maxHeight: '300px' }}>
            {dataSource.map(i => (
                <Radio.RadioItem key={i.label} checked={value === i.value} onChange={() => onItemClick(i)} aria-selected={value === i.value}>
                    <span style={{color: '#666'}}>{i.label}</span>
                </Radio.RadioItem>
            ))}
        </List>
    </Modal>
}