import React from 'react';
import Main from './pages/main'
import PartsMain from './pages/parts/main'
import PartsResult from './pages/parts/result'
import { Route, Router, Switch } from 'react-router-dom'
import History from './util/history'

function App() {
  return (
    <div className="App">
      <Router history={History}>
        <Switch>
          <Route exact path="/" component={Main} />
          <Route exact path="/parts" component={PartsMain} />
          <Route exact path="/parts/result" component={PartsResult} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
